<template>
  <div class="supplier-container">
    <el-card>
      <div slot="header" class="clearfix">
        <span>供应商管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right">新增供应商</el-button>
      </div>

      <!-- 查询条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="供应商名称">
          <el-input v-model="searchForm.supplierName" placeholder="请输入供应商名称"></el-input>
        </el-form-item>
        <el-form-item label="供应商编码">
          <el-input v-model="searchForm.supplierCode" placeholder="请输入供应商编码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 表格数据 -->
      <el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="supplierCode" label="供应商编码"></el-table-column>
        <el-table-column prop="supplierName" label="供应商名称"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'danger'">
              {{ scope.row.status === 1 ? '启用' : '禁用' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>

      <!-- 批量操作 -->
      <div v-if="selectedRows.length > 0" class="batch-operation">
        <el-button type="danger" @click="handleBatchDelete">批量删除</el-button>
      </div>
    </el-card>

    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="60%">
      <el-form :model="form" :rules="rules" ref="supplierForm" label-width="120px">
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="供应商编码" prop="supplierCode">
              <el-input v-model="form.supplierCode" placeholder="请输入供应商编码"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="供应商名称" prop="supplierName">
              <el-input v-model="form.supplierName" placeholder="请输入供应商名称" @blur="checkSupplierName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="状态" prop="status">
              <el-switch v-model="form.status" :active-value="1" :inactive-value="0"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {supplierAPI} from "@/api/api";

export default {
  name: 'Supplier',
  data() {
    return {
      // 查询条件
      searchForm: {
        supplierName: '',
        supplierCode: '',
        status: ''
      },
      // 表格数据
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      // 选中的行
      selectedRows: [],
      // 对话框
      dialogVisible: false,
      dialogTitle: '',
      // 表单数据
      form: {
        id: null,
        supplierCode: '',
        supplierName: '',
        status: 1
      },
      // 表单验证规则
      rules: {
        supplierName: [
          { required: true, message: '请输入供应商名称', trigger: 'blur' }
        ]
      },
      // 当前用户公司ID
      companyId: 1 // 实际使用时应从登录用户信息中获取
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    // 加载数据
    loadData() {
      const params = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        companyId: this.companyId,
        ...this.searchForm
      }

      supplierAPI.page(params).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.records
          this.total = res.data.total
        } else {
          this.$message.error(res.message)
        }
      }).catch(err => {
        this.$message.error('加载数据失败')
      })
    },

    // 查询
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.searchForm = {
        supplierName: '',
        supplierCode: '',
        status: ''
      }
      this.loadData()
    },

    // 分页大小改变
    handleSizeChange(size) {
      this.pageSize = size
      this.loadData()
    },

    // 当前页改变
    handleCurrentChange(current) {
      this.currentPage = current
      this.loadData()
    },

    // 选择行变化
    handleSelectionChange(rows) {
      this.selectedRows = rows
    },

    // 新增
    handleAdd() {
      this.dialogTitle = '新增供应商'
      this.form = {
        id: null,
        supplierCode: '',
        supplierName: '',
        status: 1
      }
      this.dialogVisible = true
    },

    // 编辑
    handleEdit(row) {
      this.dialogTitle = '编辑供应商'
      this.form = {...row}
      this.dialogVisible = true
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除该供应商吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        supplierAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('删除失败')
        })
      })
    },

    // 批量删除
    handleBatchDelete() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要删除选中的 ${ids.length} 个供应商吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        supplierAPI.deleteBatch(ids).then(res => {
          if (res.code === 200) {
            this.$message.success('批量删除成功')
            this.loadData()
            this.selectedRows = []
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('批量删除失败')
        })
      })
    },

    // 取消
    handleCancel() {
      this.dialogVisible = false
      this.$refs.supplierForm.resetFields()
    },

    // 提交
    handleSubmit() {
      this.$refs.supplierForm.validate((valid) => {
        if (valid) {
          const params = {
            ...this.form,
            companyId: this.companyId
          }

          if (this.form.id) {
            // 编辑
            supplierAPI.update(params).then(res => {
              if (res.code === 200) {
                this.$message.success('更新成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('更新失败')
            })
          } else {
            // 新增
            supplierAPI.add(params).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('新增失败')
            })
          }
        }
      })
    },

    // 检查供应商名称是否已存在
    checkSupplierName() {
      if (this.form.supplierName) {
        supplierAPI.checkName(this.form.supplierName, this.form.id).then(res => {
          if (res.code === 200 && !res.data) {
            this.$message.warning('供应商名称已存在')
          }
        })
      }
    }
  }
}
</script>

<style scoped>
.supplier-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}

.batch-operation {
  margin-top: 10px;
}
</style>
